/* Dark Theme */
body.dark {
  background-color: #1a202c; /* Dark mode background */
  color: #e2e8f0; /* Light text for dark mode */
}

header.dark {
  background-color: #2d3748; /* Dark header background */
  color: #e2e8f0; /* Light text */
}

NavBar.dark{
  background-color: #2d3748; /* Dark header background */
}

Footer.dark{
  background-color: #2d3748; /* Dark footer background */
  color: #e2e8f0; /* Light text */
}
button {
  background-color: #e2e8f0;
  color: #1a202c;
  border: none;
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

button.dark {
  background-color: #4a5568;
  color: #e2e8f0;
}
.icon {
  width: 24px;
  height: 24px;
  transition: transform 0.3s ease;
}

.theme-toggler:hover .icon {
  transform: scale(1.1);
}

/* Basic slider container styling */
.slick-slider {
  position: relative;
  display: block;
  user-select: none;
  touch-action: pan-y;
}

/* Slide styles */
.slick-slide {
  display: inline-block;
  height: auto;
  overflow: hidden;
}

/* Custom navigation arrows */
.slick-prev, .slick-next {
  position: absolute;
  top: 50%;
  z-index: 1;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
  background-color: #eda2bf;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.slick-prev {
  left: -50px;
}

.slick-next {
  right: -50px;
}

/* Remove default font-based arrow icons */
.slick-prev:before,
.slick-next:before {
  content: ''; /* Remove default icons */
}

/* Custom dots */
.slick-dots {
  text-align: center;
  margin-top: 10px;
}

.slick-dots li button:before {
  font-size: 12px;
  color: #eda2bf;
  opacity: 0.5;
}

.slick-dots li.slick-active button:before {
  opacity: 1;
}

